import React from "react"
import PropTypes from "prop-types"

import Nav from "./nav"
import Seo from "./seo"
import Footer from "./Footer"
import '../styles/background-image.css'
const Layout = ({ children }) => {
  return (
    <>
      <Seo />
      <Nav />
      <main>{children}</main>
      <Footer/>
    </>
  )
}

Layout.propTypes = {
  children: PropTypes.node.isRequired,
}

export default Layout
